import React, { Component } from "react";
import evalFormDataService from "../evalform.service";
import { Link } from "react-router-dom";

export default class EvalFormList extends Component {
    constructor(props) {
        super(props);
        this.onChangeSearch = this.onChangeSearch.bind(this);
        this.retrieveEvals = this.retrieveEvals.bind(this);
        this.refreshList = this.refreshList.bind(this);
        this.setActiveEval = this.setActiveEval.bind(this);
        this.removeAllEvals = this.removeAllEvals.bind(this);
        this.searchEval = this.searchEval.bind(this);

        this.state = {
            evals: [],
            currentEval: null,
            currentIndex: -1,
            search: ""
        };
    }

    componentDidMount() {
        this.retrieveEvals();
    }

    onChangeSearch(e) {
        const search = e.target.value;

        this.setState({
            search: search
        });
    }

    retrieveEvals() {
        evalFormDataService.getAll()
            .then(response => {
                this.setState({
                    evals: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    refreshList() {
        this.retrieveEvals();
        this.setState({
            currentEval: null,
            currentIndex: -1
        });
    }

    setActiveEval(evalForm, index) {
        this.setState({
            currentEval: evalForm,
            currentIndex: index
        });
    }

    removeAllEvals() {
        evalFormDataService.deleteAll()
            .then(response => {
                console.log(response.data);
                this.refreshList();
            })
            .catch(e => {
                console.log(e);
            });
    }

    searchEval() {
        evalFormDataService.findOne(this.state.searchEval)
            .then(response => {
                this.setState({
                    evals: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    getEvalForm(id) {
        evalFormDataService.get(id)
            .then(response => {
                this.setState({
                    currentEval: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    render() {
        const { searchEval, evals, currentEval, currentIndex } = this.state;

        return (
            <div className="list row">
                <div className="col-md-8">
                    <div className="input-group mb-3">
                        <input 
                            type="text"
                            className="form-control"
                            placeholer="Search"
                            value={searchEval}
                            onChange={this.onChangeSearch}
                        />
                        <div className="input-group-append">
                            <button
                                className="btn btn-outline-secondary"
                                type="button"
                                onClick={this.searchTitle}
                            >Search</button>
                        </div>
                    </div>
                </div>
                <div className="col-md-6">
                    <h4>Eval Forms List</h4>

                    <ul className="list-group">
                        {evals &&
                            evals.map((evalForm, index) => (
                                <li
                                    className={
                                        "list-group-item " +
                                            (index === currentIndex ? "active" : "")
                                    }
                                    onClick={() => this.setActiveEval(evalForm, index)}
                                    key={index}
                                >{evalForm.Contributions_value}</li>
                            ))}
                    </ul>

                    <button
                        className="m-3 btn btn-sm btn-danger"
                        onClick={this.removeAllEvals}
                    >Remove All</button>
                </div>
                <div className="col-md-6">
                    {currentEval ? (
                        <div>
                            <h4>Eval Form</h4>
                            <div>
                                <label>
                                    <strong>Peer Evaluation For:</strong>
                                </label>{" "}
                                {currentEval.StudentReciever_id}
                            </div>
                            <div>
                                <label>
                                    <strong>Disciplinary Knowledge</strong>
                                </label>{" "}
                                {currentEval.DisciplinaryKnowledge_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Intellectual and Creative Skills</strong>
                                </label>{" "}
                                {currentEval.IntellectualCreativeSkills_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Interpersonal Skills</strong>
                                </label>{" "}
                                {currentEval.InterpersonalSkills_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Global Citizenship</strong>
                                </label>{" "}
                                {currentEval.GlobalCitizenship_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Personal Mastery</strong>
                                </label>{" "}
                                {currentEval.PersonalMastery_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Contributions</strong>
                                </label>{" "}
                                {currentEval.Contributions_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Contribution Percentage</strong>
                                </label>{" "}
                                {currentEval.Contributionpercent_value}
                            </div>
                            <div>
                                <label>
                                    <strong>Improvement</strong>
                                </label>{" "}
                                {currentEval.Improvement_value}
                            </div>

                            {/* <Link
                                to={"/evalforms/" + currentEval.id}
                                className="badge badge-warning"
                            >
                            Edit</Link> */}
                            <button 
                                className="badge badge-danger mr-2"
                                onClick={this.getEvalForm(currentEval.id)}>Edit</button>
                        </div>
                    ) : (
                        <div>
                            <br/>
                            <p>Please click on an Eval Form.</p>
                        </div>
                    )}
                </div>
            </div>
        );
    }
}